import { cn } from "@/lib/utils"
import { ArrowUp01Icon, ArrowDown01Icon, Minus01Icon } from "@hugeicons/react"

interface KPICardProps {
  value: string | number
  label: string
  sublabel?: string
  className?: string
  tendencia?: "up" | "down" | "neutral"
  variacionTexto?: string
  isBueno?: boolean
}

export function KPICard({ value, label, sublabel, className, tendencia, variacionTexto, isBueno }: KPICardProps) {
  const getTrendIcon = () => {
    if (!tendencia || tendencia === "neutral") {
      return <Minus01Icon size={16} className="text-foreground" />
    }
    if (tendencia === "up") {
      return <ArrowUp01Icon size={16} className="text-foreground" />
    }
    return <ArrowDown01Icon size={16} className="text-foreground" />
  }

  const getTrendColor = () => {
    return "text-muted-foreground"
  }

  return (
    <div className={cn("py-4 px-5 border border-border rounded-lg bg-background", className)}>
      <div className="flex flex-col gap-0.5">
        <span className="text-2xl font-semibold tracking-tight text-foreground">
          {value}
        </span>
        <span className="text-sm text-muted-foreground">{label}</span>
        {sublabel && (
          <span className="text-xs text-muted-foreground/70">{sublabel}</span>
        )}
        {variacionTexto && (
          <div className={cn("flex items-center gap-1 text-xs mt-1", getTrendColor())}>
            {getTrendIcon()}
            <span>{variacionTexto} vs dic</span>
          </div>
        )}
      </div>
    </div>
  )
}
